export default {
	data() {
		return {
			username: '',
			password: ''
		}
	},
	onLoad() {

	},
	methods: {
		handleRegist() {
			// 简单验证
			if (this.username.length === 0) {
				uni.showToast({
					title: '用户名不能为空',
					icon: 'none',
					duration: 2000
				})
				return
			}
			if (this.password.length === 0) {
				uni.showToast({
					title: '密码不能为空',
					icon: 'none',
					duration: 2000
				})
				return
			}
			uni.showLoading({
				title: '请稍等...'
			})
			uni.request({
				url: getApp().globalData.baseUrl + '/regist',
				method: "POST",
				data: {
					username: this.username,
					password: this.password
				},
				success: (res) => {
					let status = res.data.status
					if (status === 200) {
						uni.showToast({
							title: " 用户注册成功! 页面跳转中... ",
							icon: 'none',
							duration: 2000
						})
						getApp().globalData.setGlobalUserInfo(res.data.data)
						uni.switchTab({
							url: '../index/index'
						})
					} else if (status === 500) {
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 2000
						})
					}
				}
			})
		}
	}
}
